.profile-h2 {
    text-align: center;
    font-size: 35px;
}

.profile-card {
    max-width: 900px;
    margin: 40px auto;
    background: #fff;
    padding: 34px 32px;
    border-radius: 16px;
    box-shadow: 0 4px 14px rgba(0, 0, 0, 0.08);
    font-family: 'Inter', sans-serif;
    color: #0d2c4a;
}

.profile-summary {
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 12px;
    margin-bottom: 20px;
}

.profile-summary h3 {
    margin-bottom: 10px;
}

.profile-summary ul {
    list-style-type: none;
    padding: 0;
}

.profile-summary li {
    margin: 6px 0;
}

.profile-summary a {
    text-decoration: none;
    color: rgb(0, 43, 134);
}

.profile-summary a:hover {
    text-decoration: underline;
}

.profile-section {
    margin-bottom: 24px;
}

.profile-section h3 {
    margin-bottom: 12px;
    color: rgb(0, 43, 134);
}

.profile-container table {
    width: 100%;
    border-collapse: collapse;
}

.profile-container th,
.profile-container td {
    text-align: left;
    padding: 10px 12px;
    border-bottom: 1px solid #e0e0e0;
}

.profile-container th {
    background-color: #f5f9fc;
    font-weight: 600;
}

.profile-container tr:hover {
    background-color: #f0f7ff;
}

.data-img {
    display: flex;
    justify-content: flex-end;
    position: relative;
}

.data-img img {
    position: absolute;
    bottom: 10px;
    right: 60px;
    width: 9rem;
    height: 8.5rem;
}

@media only screen and (max-width: 768px) {
    .data-img img {
        position: absolute;
        bottom: 60px;
        right: -10px;
        width: 4rem;
        height: 4rem;
    }

    .profile-card {

        max-width: 330px;
        margin: 50px auto;
        padding: 34px 26px;
    }

    .profile-h2 {
        text-align: center;
        font-size: 18px;
        margin-top: 3rem;
    }

    .email-data {
        display: none;
    }

    th,
    td {
        font-size: 15px;

    }

    .profile-section {

        line-height: 1.4;
    }


}